* {
  margin: 0;
  padding: 0;
  color: inherit;
  font: inherit;
  line-height: 1.5;
  font-family: 'Roboto', sans-serif;
  font-size: 1em;
}

.layout1 {
  display: flex;
  flex-direction: row;
}
.layout2 {
  flex: 2;
  display: flex;
  flex-direction: column;
}

/* Top of contents */
.container {
  width: 100%;
  margin: 0 auto;
  color: rgb(50,51,53);
}

#bars {
  display: none;
  font-size: 16px;
  position: relative;
  top: 1em;
  left: -3em;
  cursor: pointer;
}

.content-header {
  font-size: 16px;
  margin-top: 2em;
  display: flex;
  justify-content: space-between;
  padding-bottom: 1em;
  border-bottom: 2.5px solid;
}
.content-header span {
  font-weight: 500;
}

/* common */
.active {
  background-color: #d52349;
  padding: 1em 0em 1em 1em;
  border-radius: 10px;
}