.people {
    padding: 0em 2.5em 0em 2.5em;
}
.people h1 {
    font-size: 2em;
    font-weight: 600;
    margin-top: 1em;
}
.people .people-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 1em;
    padding-bottom: 2em;
    border-bottom: 1px solid rgba(0,0,0,0.2);
    justify-content: space-between;
}
.people .people-container h2 {
    font-size: 1.5em;
    font-weight: 600;
    margin-top: 1em;
    margin-bottom: 0.25em;
    flex-basis: 100%;
}
.people .people-container .person {
    width: 32%;
    display: flex;
    flex-direction: row;
    gap: 1em;
}
.people .people-container .person .image {
    width: 250px;
    height: 200px;
}
.people .people-container .person .image img {
    display: block;
    width: 100%;
    height: 100%;
}
.people .people-container .person .bio {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 1em;
}
.people .people-container .person .bio .name {
    font-size: 17px;
    font-weight: 600;
}
.people .people-container .person .bio .state {
    text-transform: capitalize;
}
.people .people-container-last {
    display: flex;
    flex-direction: column;
    gap: 1em;
    padding-bottom: 2em;
}
.people .people-container-last h2 {
    font-size: 1.5em;
    font-weight: 600;
    margin-top: 1em;
    margin-bottom: 0.25em;
    flex-basis: 100%;
}
.people .people-container-last p {
    font-size: 1em;
}