.lecture {
    padding: 0em 2.5em 0em 2.5em;
}
.lecture .lecture-type {
    margin-top: 2em;
    margin-bottom: 1em;
}
.lecture .lecture-type h2 {
    font-size: 2em;
    font-weight: 600;
    margin-top: 0.5em;
    margin-bottom: 1em;
}
.lecture .lecture-type h3 {
    font-size: 1em;
    font-weight: 600;
    margin-top: 2em;
    margin-bottom: 1em;
}
.lecture .lecture-type ul li {
    margin-bottom: 1em;
    position: relative;
    left: 1.5em;
}
.lecture .lecture-type ul li span {
    font-style: italic;
}